.Tag {
    width: 18em;
    height: 3em;
    text-align: center;
    border-radius: 0.2em;
    margin: auto;
    margin-top: 1em;
    margin-left: 0.5em;
    margin-right: 0.5em;
    border-left: #8156bc solid;
    display: block;

    background: -webkit-gradient(linear, left top, right top, color-stop(50%, #8156bc), color-stop(50%, #191919));

    background: -webkit-linear-gradient(left, #8156bc 50%, #191919 50%);

    background: -o-linear-gradient(left, #8156bc 50%, #191919 50%);

    background: linear-gradient(to right, #8156bc 50%, #191919 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    -webkit-transition: all .5s ease-out;
    -o-transition: all .5s ease-out;
    transition: all .5s ease-out;
}

.Tag:hover {
    background-position: left bottom;
}

.TagName {
    margin: 1em;
    color: #cccccc;
}

@media (max-width:320px){
    .Tag {
        width: 10em;
    }
}


